  background-position:center;
  background-repeat:no-repeat;
  background-size:cover;
}
#banners {
  padding-top:100px;
}
.launch-market-place-popover {
  background-color:#f19150;
}
.launch-market-place-popover .popover-body {
  color:#ffffff;
  font-size:1.2em;
}
.launch-market-place-popover .popover-arrow::after {
  border-bottom-color:#f19150;
}
ul.dash {
  list-style: none;
  margin-left: 0;
  padding-left: 1em;
}
ul.dash > li:before {
  display: inline-block;
  content: "-";
  width: 1em;
  margin-left: -1em;
}
.custom-table-1 {
  border:3px solid #000000;
}
.custom-table-1 th, .custom-table-1 td {
  border-bottom:2px solid #000000;
  border-right:2px solid #000000;
  vertical-align:middle;
}
.partner-logo:not(:hover) .normal {
  display:block;
}
.partner-logo:not(:hover) .hovered {
  display:none;
}
.partner-logo:hover .normal {
  display:none;
}
.partner-logo:hover .hovered {
  display:block;
}
#header.scrolled {
  background-color:rgba(52,49,49,0.85)!important;
}
input::-webkit-outer-spin-button,
