#banners {
  padding-top:0;
}
.page-min-height {
  min-height:100vh;
}
.tokenomics-card-body {
  margin-top:-105px;
}
.tokenomics-card-body .bg-white {
  min-height:479px;
}
.tokenomics-arrow {
  width:30px;
  top:50px;
  right:-40px
}
.feature-icons {
  min-width:50px;
  width:50px;
}
#header {
  background-color: rgba(0,0,0,0)!important;
  border:0;
  top:0;
  left:0;
  z-index:10;
  transition:0.3s
}
.countdown {
  min-width:60px;
}

.own-token {
  width: 15%;
  margin: 1.5rem auto 1rem;
}
#addresses a:hover {
  color:#bc3326;
}
.padding-after-nav {
  padding-top:68px!important;
}
#october-targets {
  width:70px;
}
#monthly-targets-bg {
  background-size:cover;
}

/* SM */
@media(min-width:576px) {
  .page-min-height {
    min-height:100vh;
  }
  .tokenomics-card-body {
    margin-top:-130px;
  }
  .tokenomics-card-body .bg-white {
    min-height:490px;
  }
  .tokenomics-arrow {
    width:40px;
    top:60px;
    right:-53px
  }
  .padding-after-nav {
    padding-top:117px!important;
  }
  #monthly-targets-bg {
    background-size:contain;
  }
  #october-targets {
    width:100px;
  }
}
/* MD */
@media(min-width:768px) {
  #banners {
    padding-top:109.8px;
  }
  .page-min-height {
    min-height:calc(100vh - 109.8px);
  }
  .tokenomics-card-body {
    margin-top:-140px;
  }
  .tokenomics-card-body .bg-white {
    min-height:423px;
  }
  .tokenomics-arrow {
    width:45px;
    top:60px;
    right:-78px
  }
  #header {
    background-color: #ffffff!important;
    border-bottom:1px solid #dee2e6!important;
  }
  .padding-after-nav {
    padding-top:117px!important;
  }
  #october-targets {
    width:150px;
  }
}
/* LG */
@media(min-width:992px) {
  #banners {
    padding-top:83px;
  }
  .page-min-height {
    min-height:calc(100vh - 83px) !important;
  }
  .tokenomics-card-body {
    margin-top:-120px;
  }
  .tokenomics-card-body .bg-white {
    min-height:512px;
  }
  .tokenomics-arrow {
    width:45px;
    top:40px;
    right:-55px
  }
  .feature-icons {
    min-width:80px;
    width:80px;
  }
  .countdown {
    min-width:105px;
  }
  .padding-after-nav {
    padding-top:83px!important;
  }
  #burn-header-container {
    min-height:calc(100vh - 83px);
  }
  #october-targets {
    width:230px;
  }
}
/* XL */
@media(min-width:1200px) {
  .tokenomics-card-body {
    margin-top:-145px;
  }
  .tokenomics-card-body .bg-white {
    min-height:536px;
  }
  .tokenomics-arrow {
    width:45px;
    top:60px;
    right:-55px
  }
  .countdown {
    min-width:116px;
  }
}
/* XXL */
@media(min-width:1400px) {
  .tokenomics-card-body {
    margin-top:-120px;
  }
  .tokenomics-card-body .bg-white {
    min-height:454px;
  }
  .tokenomics-arrow {
    width:65px;
    top:55px;
    right:-90px
  }
}

@media (max-width: 991px) {
  .own-token-desc {
    width: 100% !important;
  }
  #det-section {
    flex-direction: column;
  }
  #det-section > div:not(:last-child) {
    margin-bottom: 1.5rem;
  }
  #addresses .alegreya-bold-italic,
  #exchanges .alegreya-bold-italic {
    text-align: center;
  }
}

@media (max-width: 768px) {
  #exc-section {
    flex-direction: column;
    align-items: center !important;
  }
  #exc-section > div:not(:last-child) {
    margin-bottom: 3em;
  }
  .header-token#header {
    background-color:rgba(52,49,49,0.85) !important;
  }
  .own-token {
    width: 30% !important;
  }
  .own-token-desc {
    font-size: 1.2em;
  }
}

@media (max-width: 576px) {
  .own-token-title {
    font-size: 2.5em;
  }
}